import './MonthChunk.scss';

import { format } from 'date-fns';
import React from 'react';

export interface PropsMonthChunk {
  date: Date;
  appsCount: number;
}

const MonthChunk = ({ date, appsCount }: PropsMonthChunk) => {
  return (
    <span styleName="monthChunk">
      <div styleName="weekday">{format(date, "EEEEEE")}</div>
      <div styleName="monthday">{format(date, "d")}</div>
      <div styleName="appsCount">{appsCount}</div>
    </span>
  );
};

export default MonthChunk;
