import React from 'react';
import { render } from 'react-dom';

import Calendar from './components/Calendar';
import VersionBox from './components/VersionBox';
import { register } from './serviceWorker';
import { LazyTaskContainer } from './stores/lazytask';
import { MockInfo } from './stores/mock';
import { UIInfo } from './stores/ui';
import { UsersInfo } from './stores/users';

const App = () => {
  return React.useMemo(
    () => (
      <>
        <MockInfo.Provider>
          <LazyTaskContainer.Provider>
            <UIInfo.Provider>
              <UsersInfo.Provider>
                <Calendar />
              </UsersInfo.Provider>
            </UIInfo.Provider>
          </LazyTaskContainer.Provider>
        </MockInfo.Provider>
        <VersionBox />
      </>
    ),
    []
  );
};

render(<App />, document.getElementById("root"));

register();
