import './MonthRow.scss';

import { addDays } from 'date-fns';
import React from 'react';

import { randomInt } from '../../../../assembly/random';
import MonthChunk from './MonthChunk';

const MonthRow = () => {
  const now = new Date();
  return (
    <div styleName="monthRow">
      {new Array(20).fill(null).map((_, i) => (
        <MonthChunk
          key={i}
          date={addDays(now, i)}
          appsCount={randomInt(0, 150)}
        />
      ))}
    </div>
  );
};

export default MonthRow;
